import React, { Component } from 'react';
import Papa from 'papaparse';

export default class tango extends Component  {
    constructor (props) {
        super(props);
        this.state = {
            TangoList:undefined,
        };
    }

    componentDidMount(){
        this.GetCSV();
        this.ClickMondaihe();
    }
    /*
    csvファイル読み出し.
    【入力】
        なし
    【出力】
        なし
    */
    GetCSV = () =>{
        var reqq = new XMLHttpRequest();
        reqq.open("get", 'tango.csv',true);
        reqq.send(null);

        reqq.onload = () =>{
            Papa.parse(reqq.responseText,{
                header:true,
                delimiter:',',

                complete:(buf) =>{
                    console.log(buf.data);
                    this.setState({TangoList:buf.data});
                }
            })
        }
    }

    /*
    問題ページへの変更.
    【入力】
        なし
    【出力】
        なし
    */
    ClickMondaihe = () =>{
        this.props.history.push({
            pathname:"/SelectMondai",                       // 問題ページへの移行.
            state:{Num:this.state.TangoList}                // 問題ページへ渡す問題リスト.
        });
    }

    render() {
        return (<div></div>)
    }
  }