import React, { Component } from 'react';
import { BrowserRouter, Route, Switch, Link } from 'react-router-dom';
import Tango from './tango';
import Resistword from './resistword'
import SelectMondai from './selectMondai'
class library extends Component  {
    render() {
        return (<div>
            <BrowserRouter basename={process.env.PUBLIC_URL + '/'}>
                <Switch>
                    <Route exact path='/' component={Tango} />
                    <Route path='/Resister' component={Resistword} />
                    <Route path='/SelectMondai' component={SelectMondai} />
                </Switch>
            </BrowserRouter>
        </div>);
    }
  }

  export default library;