import React, { Component } from 'react';
import Papa from 'papaparse';
import "./resistword.css";

export default class resister extends Component  {
    constructor (props) {
        super(props);
        this.state = {
            TangoList:undefined,
            ResistTangoList:undefined,
        };
    }

    /*
    csvファイル読み出し.
    【入力】
        なし
    【出力】
        なし
    */
    GetCSV = () =>{
        var reqq = new XMLHttpRequest();
        reqq.open("get", 'tango.csv',true);
        reqq.send(null);

        reqq.onload = () =>{
            Papa.parse(reqq.responseText,{
                header:true,
                delimiter:',',

                complete:(buf) =>{
                    console.log(buf.data);
                    this.setState({TangoList:buf.data});
                }
            })
        }
    }

    componentDidMount(){
        this.GetCSV();
    }

    /*
    全てのチェックボックスにチェックを入れる/外す.
    【入力】
        なし
    【出力】
        なし
    */
    CheckedAll = () =>{
        console.log("Check All box");

        // 単語リスト分のチェックボックスにチェックを入れるためのループ.
        for(var i in this.state.TangoList){
            var IdName = "chbox"+i;             // id名.
            document.getElementById(IdName).checked = 
                document.getElementById("Allcheck").checked;    // id名にチェックを入れる.
        }
    }

    /*
    問題ページへの変更.
    【入力】
        なし
    【出力】
        なし
    */
    ClickMondaihe = () =>{
        var list = [];
        var count = 0;

        // チェックボックスが入っているidを読み出し、リストにセット.
        for(var i in this.state.TangoList){
            var IdName = "chbox"+i;
            
            if(document.getElementById(IdName).checked === true){
                count ++ ;
                list.push(this.state.TangoList[i]);
            }
        }

        // チェックを入れた問題数が3以下であれば選択させない.
        if(count > 3){
            this.setState({ResistTangoList:list})
            this.props.history.push({
                pathname:"/SelectMondai",       // 問題ページへの移行.
                state:{Num:list}                // 問題ページへ渡す問題リスト.
            });
        }
    }

    /*
    問題選択テーブルに問題選択リストをセット.
    【入力】
        なし
    【出力】
        なし
    */
    SetTangoList = () =>{
        var list = [];
        const {TangoList} = this.state;

        // データを1つずつ読み出し、問題リストにセットしていく.
        for(var i in TangoList){
            var japanese = TangoList[i]["Japanese"];
            var english = TangoList[i]["English"];
            var idname = "chbox"+i;

            // 問題リストを追加・作成.
            list.push(
                <tbody>
                    <td><input type="checkbox" id={idname} className="listcheck" /></td>
                    <td>{japanese}</td>
                    <td>{english}</td>
                </tbody>
            )            
        }
        return list;
    }

    render() {
        var list = this.SetTangoList(list);
        
        return (
            <div>
                <p className="TangoDaimei">単語登録ページ</p>
                <button className="sidebutton1" onClick={this.ClickMondaihe}>単語問題へ⇒</button>
                <div className="AllCheckFont">
                    <input type="checkbox" id="Allcheck" className="Allcheck" onClick={this.CheckedAll}/>全選択<br/>
                </div>
                <table border="1">
                    <tr>
                        <th>チェック</th>
                        <th>英語</th>
                        <th>日本語</th>
                    </tr>
                    {list}           
                </table>
            </div>
        );
    }
}