import React, { Component } from 'react';
//import { BrowserRouter, Route, Switch, Link } from 'react-router-dom';
import ReactCardFlip from 'react-card-flip';
import Papa from 'papaparse';
import "./tango.css";
import BlankCard from './image/BlankCard.png';
import OK from "./image/OK.png";
import NG from "./image/NG.png";
import Blank from "./image/blank.png";
import DocumentMeta from 'react-document-meta';

export default class tango extends Component  {
    constructor (props) {
        super(props);
        this.state = {
            isFlipped: false,
            Tango:undefined,
            English:"",
            Japanese:"",
            Num:0,
            Select:["", "", ""]
        };
    }

    /*
    問題切り替え・表示用.
    【入力】
        Num：ランダム数
        Tango：単語集
    【出力】
        なし
    */
    SelectMsg = (Num, Tango) =>{
        var one = Num - 1;
        var three = Num + 1;
        if(one < 0){one = Tango.length - 1;}
        if(three > (Tango.length -1)){three = 0;}
        this.SetTango(one, Num, three, Tango);

    }

    /*
    単語を選択ボタンにセット.
    【入力】
        One：1番目の選択
        Two：2番目の選択
        Three：3番目の選択
    【出力】
        なし
    */
    SetTango =(One, Two, Three, Tango) =>{
        var a = Math.floor(Math.random() * 5);  // セットするランダム.
        var Msg = this.state.Select.slice();    // 選択するボタンにセットする回答.
        console.log("One:"+ One + " Two:"+Two + " Three:"+Three);   // 番号確認用ログ.
        
        // ボタンにセットする回答切り替え.
        if(a === 0){                            
            Msg[0] = Tango[One].Japanese;
            Msg[1] = Tango[Two].Japanese;
            Msg[2] = Tango[Three].Japanese;
        }else if(a === 1){
            Msg[0] = Tango[Two].Japanese;
            Msg[1] = Tango[Three].Japanese;
            Msg[2] = Tango[One].Japanese;
        }else if(a === 2){
            Msg[0] = Tango[Three].Japanese;
            Msg[1] = Tango[One].Japanese;
            Msg[2] = Tango[Two].Japanese;
        }else if(a === 3){
            Msg[0] = Tango[Two].Japanese;
            Msg[1] = Tango[One].Japanese;
            Msg[2] = Tango[Three].Japanese;
        }else if(a === 4){
            Msg[0] = Tango[One].Japanese;
            Msg[1] = Tango[Three].Japanese;
            Msg[2] = Tango[Two].Japanese;          
        }else{
            Msg[0] = Tango[Three].Japanese;
            Msg[1] = Tango[Two].Japanese;
            Msg[2] = Tango[One].Japanese;               
        }
        this.setState({Select:Msg})             // 回答をセットする. 
    }

    /*
    csvファイル読み出し.
    【入力】
        なし
    【出力】
        なし
    */
    GetCSV = () =>{
        var reqq = new XMLHttpRequest();
        reqq.open("get", 'tango.csv',true);     // 単語リストが格納されたcsvファイルを開く.
        reqq.send(null);
        reqq.onload = () =>{                    // 読み出したファイルから文字列を読み取る.
            Papa.parse(reqq.responseText,{
                header:true,
                delimiter:',',

                complete:(buf) =>{              // 読み出したデータをbufにセット.
                    console.log(buf.data);      
                    this.setState({Tango:buf.data})     // 変数にデータをセットする.
                    this.SetNextWord(buf.data);
                }
            })
        }
    }


    /*
    最初の問題に対する回答をセットする.
    【入力】
        単語データ
    【出力】
        なし
    */    
    SetNextWord = (Tango) =>{
        console.log(Tango[0].English)
        var a = Math.floor(Math.random() * (Tango.length));
        this.setState({Num:a})                          // 単語番号.
        this.setState({English:Tango[a].English})       // 英語文字セット.
        this.setState({Japanese:Tango[a].Japanese})     // 日本語文字セット.
        this.SelectMsg(a, Tango);                       // 回答(3択)をセット.
    }

    /*
    最初の問題に対する回答をセットする.
    【入力】
        単語データ
    【出力】
        なし
    */    
    componentDidMount(){
        this.setState({Tango:this.props.location.state.Num});
        this.SetNextWord(this.props.location.state.Num);
    }

    componentDidMount(){
        this.GetCSV();
    }

    /*
    回答ボタンを押下.
    【入力】
        buttonName：選択したボタン
    【出力】
        なし
    */  
    nextClick = (buttonName) =>{
        const {Tango} = this.state;     // 単語リストが格納されたデータを読み取り.
        
        this.setState(prevState => ({ isFlipped: !prevState.isFlipped }));  // 問題に出している英語を日本語にフリップ.
        if(buttonName === this.state.Japanese){                             // 日本語を比較して正解してれば、OKを間違っていればNGを表示.
            document.getElementById("OKNG").src = OK;
        }else{
            document.getElementById("OKNG").src = NG;
        }
        document.getElementById("button1").disabled = true;
        document.getElementById("button2").disabled = true;
        document.getElementById("button3").disabled = true;

        //1.5秒後に関数を抜ける.
        setTimeout(() =>{
            document.getElementById("card").style.visibility = "hidden";        // 次の問題の日本語が見えないように隠す.
            this.setState(prevState => ({ isFlipped: !prevState.isFlipped }));  // 問題を英語側にフリップする.
            setTimeout(() =>{                                                   // 英語側にフリップするまでは隠しておく.
                this.SetNextWord(Tango);                                        // 次の単語データをセットする.
                document.getElementById("card").style.visibility = "visible";   // 問題を見えるようにする.
                document.getElementById("OKNG").src = Blank;                    // OK/NGのメッセージを空欄にする.
                document.getElementById("button1").disabled = false;            // ボタンが押下できるようにする.
                document.getElementById("button2").disabled = false;            // ボタンが押下できるようにする.
                document.getElementById("button3").disabled = false;            // ボタンが押下できるようにする.
            },300)
        },1500)
    }

    /*
    登録用へのボタンを押下.
    【入力】
        なし
    【出力】
        なし
    */ 
    ClickNextPage = () =>{
        this.props.history.push({           // ページ切り替えのためのpush読み出し.
            pathname:"/Resister",           // 移動先ページをセット.
            state:{Num:this.state.Tango}    // 移動先ページへ単語データを渡す.
        });
    }

    /*
    カードフリップモーション用関数.
    【入力】
        なし
    【出力】
        なし
    */ 
    CardFlipped = () => {
        return(
            <div>
                <ReactCardFlip isFlipped={this.state.isFlipped}  flipSpeedFrontToBack={1.0}
                    flipSpeedBackToFront={1.0} flipDirection="vertical" infinite="true" width="300px">
                    <div id="card">
                        <img id="card" src={BlankCard} alt="card" className="cardSize" />
                        <p>{this.state.English}</p>                                         
                    </div>
                    <div id="card">
                        <img id="card" src={BlankCard} alt="card" className="cardSize" />
                        <p>{this.state.Japanese}</p>                                        
                    </div>         
                </ReactCardFlip>
            </div>
        )
    }

    render() {
        const Msg = this.state.Select.slice();
        const button1 = Msg[0];
        const button2 = Msg[1];
        const button3 = Msg[2];

        const meta = {
            title: 'Elsammitの英単語アプリ作成してみた',
            description: 'React初心者が英単語アプリを作ってみました。選択式でクイズを答えるだけ！！ゲーム感覚で単語が覚えられるよ',
            canonical: 'http://example.com/path/to/page',
            meta: {
                charset: 'utf-8',
                name: {
                    keywords: 'react,meta,document,html,tags'
                }
            }
        };

        return (<div>
            <DocumentMeta {...meta}>
            
            <h1  className="TangoDaimei">単語帳アプリ</h1>
            <div className="cardPlace">
                {this.CardFlipped()}
                <button id="button1" className='NextButton1' onClick={ () => this.nextClick(button1) }>{button1}</button>
                <button id="button2" className='NextButton2' onClick={ () => this.nextClick(button2) }>{button2}</button>
                <button id="button3" className='NextButton3' onClick={ () => this.nextClick(button3) }>{button3}</button>
                <img className="seikai" id="OKNG" src={Blank} alt="" width="200" height="200" />
            </div>
            </DocumentMeta>
            <button className="NextPageButton" onClick={this.ClickNextPage}>登録内容変更⇒</button>
          </div>
        )
      }
  }